import $ from '../core';
//data-slide-to -          
/**
 *  carousel -     html .   .
 * @param {*} auto 
 * @param {*} autoTime 
 */
$.prototype.carousel = function(auto = false, autoTime = 3000){
    for(let i = 0; i < this.length; i++){
        
        //     carousel-inner
        let width = window.getComputedStyle(this[i].querySelector('.carousel-inner')).width,
            slides = this[i].querySelectorAll('.carousel-item'),//  
            slidesField = this[i].querySelector('.carousel-slides'), //        
            dots = this[i].querySelectorAll('.carousel-indicators li');//   
            

            slidesField.style.width = 100 * slides.length + '%';//      
        slides.forEach(slide => {
            slide.style.width = width; //    
        });

        let offset = 0; //   ,     slidesField
        let slideIndex = 0; // 
        let paused;//   

        $(this[i].querySelector('[data-slide="next"]')).click((e) => {
            //    next
            e.preventDefault();
            //  ,
            //      offset       
            if(offset == (+width.replace(/\D/g, '') * (slides.length -1))){
                offset = 0;// offset   
            } else{
                offset += +width.replace(/\D/g, '');//  offset   
            }

            slidesField.style.transform = `translateX(-${offset}px)`;// 

            //  
            if(slideIndex == slides.length-1){
                //        next, -  1 
                slideIndex = 0;
            }else{
                //  
                slideIndex++;
            }

            //   active  
            dots.forEach(dot => dot.classList.remove('active'));
            dots[slideIndex].classList.add('active');

        });

        $(this[i].querySelector('[data-slide="prev"]')).click((e) => {
            e.preventDefault();
            //   1     prev       
            if(offset == 0){
                offset = +width.replace(/\D/g, '') * (slides.length -1);
            } else{
                offset -= +width.replace(/\D/g, '');
            }

            slidesField.style.transform = `translateX(-${offset}px)`;// 

            //   

            if(slideIndex == 0){
                slideIndex = slides.length-1;
            }else{
                slideIndex--;
            }
            
            //    active  
            dots.forEach(dot => dot.classList.remove('active'));
            dots[slideIndex].classList.add('active');
        });

        //     
        let sliderId = this[i].getAttribute('id');
        $(`#${sliderId} .carousel-indicators li`).click((e)=>{
            //  ,  
            let slideTo = e.target.getAttribute('data-slide-to');//    

            slideIndex = slideTo;//     
            
            offset = +width.replace(/\D/g, '') * slideIndex;
            slidesField.style.transform = `translateX(-${offset}px)`;// 

            //  
            dots.forEach(dot => dot.classList.remove('active'));
            dots[slideIndex].classList.add('active');

        });

        //   
        let _autoPlay= () =>{
            paused = setInterval(()=>{
                $(this[i].querySelector('[data-slide="next"]')).click();
            }, autoTime);
        };
        
        if (auto){
            _autoPlay();
            //   
            this[i].addEventListener('mouseenter', ()=>clearInterval(paused));
            this[i].addEventListener('mouseleave', ()=>_autoPlay());
        } 

    }
};

// $('.carousel').carousel();


/**
 * createCarousel - ,     ,  js;
 * @param {Object} Object  ,  sliderId -   ,
 * count -  , settings  -      ,   alt;
 * auto -    , autoTime -   .
 * {    
 *      sliderId,
 *      count: 3,
 *      auto = true,
 *      autoTime = 3000,
 *      settings[
 *          [
 *              "https://example.jpg",
 *              "img1"
 *          ],
 *          [
 *              "https://example1.jpg",
 *              "img2"
 *          ]
 *      ]
 * }
 */

$.prototype.createCarousel = function({sliderId,count,auto,autoTime,settings} = {}){
    for(let i = 0; i < this.length; i++){
        let carousel = document.createElement('div');
        carousel.classList.add('carousel','mt-20');
        carousel.setAttribute(`id`,`${sliderId}`);

        carousel.innerHTML +=`
                <ol class="carousel-indicators">
                </ol>
                <div class="carousel-inner">
                    <div class="carousel-slides">
                    </div>
                </div>
                <a href="#" class="carousel-prev" data-slide="prev">
                    <span class="carousel-prev-icon">&lt;</span>
                </a>
                <a href="#" class="carousel-next" data-slide="next">
                    <span class="carousel-next-icon">&gt;</span>
                </a>
            `;

        this[i].append(carousel);
        

        for(let j = 0; j < count; j++){
            let [src,alt] = settings[j];
            //  
            this[i].querySelector('ol').innerHTML +=`
                <li data-slide-to="${j}"></li>
            `;
            if(j == 0){
                this[i].querySelector('ol li').classList.add('active');
            }
            //  
            this[i].querySelector('.carousel-inner .carousel-slides').innerHTML +=`
                <div class="carousel-item">
                    <img src="${src}" alt="${alt}">
                </div>
            `;

        }
        
        $(carousel).carousel(auto,autoTime);
    }
};





    