/**
 *  index -    .    
 * ,   ,    .
 * ..     ,   ,  
 *          
 * @returns {number}
 * @example
 * $(div).click(function(){
 *      console.log($(this).index());
 * })
 * //=> 2 -   div  
 */
$.prototype.index = function(){
    let parent = this[0].parentNode,
        childs = [...parent.children];//    
    //parent  
    //childs -    

    let findMyIndex = (item) =>{
        //  items   
        return item == this[0];
    };

    return childs.findIndex(findMyIndex);
    //   true,  findIndex    - 

};

/**
 *  find -     ,   
 * @param {*} selector 
 * @returns Object -  .    
 * @example
 * $('div').eq(2).find('.active');
 * //=> {0: div.active, 1: div.active, length:2}
 */
$.prototype.find = function(selector){
    let numberOfItems = 0; //   
    let copy = Object.assign({},this);//      
    let counter = 0; //     this

    for (let i = 0; i < copy.length; i++){
        let arr = copy[i].querySelectorAll(selector);
        //    arr  
        if (arr.length == 0){
            continue;
        }
        //   ,       this
        //  0
        for(let j = 0; j < arr.length; j++){
            this[counter] = arr[j];
            counter++;
        }

        numberOfItems += arr.length;
    }

    this.length = numberOfItems; //      

    let objLength = Object.keys(this).length; // -    

    //  ,    
    for(;numberOfItems < objLength; numberOfItems++){
        delete this[numberOfItems];
    }

    return this;
};

/**
 *  closest -     (  ), 
 *    CSS-,      $() 
 * @param {*} selector 
 * @returns Object
 * @example
 * $('.some').closest('.find');
 * //=> {0: div.find, 1:div.find, length: 2}
 * 
 * $('.some').closest('.findqw');
 * //=>{0: empty, 1: empty, length: 2}
 */
$.prototype.closest = function(selector){
    let counter = 0;

    for (let i = 0; i < this.length; i++){
        if(this[i].closest(selector) === null){
            this[i] = 'empty';
            counter++;
        }else{
            this[i] = this[i].closest(selector);
            counter++;
        }
        
    }

    let objLength = Object.keys(this).length;
    
    for(;counter < objLength; counter++){
        delete this[counter];
    }
    return this;

};

/**
 *  siblings -    ,    
 * @returns Object -    
 * @example 
 * $('.some').eq(0).siblings());
 * //=> {0: div.some, 1: div.some length: 2}
 * 
 * $('.find').siblings());
 * //=>{0: div, 2: div.active, length: 2};
 */
$.prototype.siblings = function(){
    let counter = 0,
        numbersOfItems = 0,
        copy = Object.assign({},this);

    for(let i = 0; i < this.length; i++){
        let arr = copy[i].parentNode.children; //    

        for(let j = 0; j < arr.length; j++){
            if(copy[i] === arr[j]){
                continue;
                //   copy[i]     
            }
            this[counter] = arr[j];
            counter++;
        }
        numbersOfItems += arr.length - 1;//- 1 ..     
    }

    this.length = numbersOfItems;

    let objLength = Object.keys(this).length;
    for(;numbersOfItems < objLength;numbersOfItems++){
        delete this[numbersOfItems];
    }
    return this;
};