import $ from '../core';
/**
 *  animateOverTime -  ,  
 * @param {number} dur -     
 * @param {function} cb - ,  
 * @param {*} fin -        
 *  
 * @returns {function} _animateOverTime;
 */
$.prototype.animateOverTime = function(dur,cb,fin){
    let timeStart;
    //   requestAnimationFrame   ,   
    //  ,    ,  ,
    //  ,       

    function _animateOverTime(time){
        //_animateOverTime -    requestAnimationFrame
        if(!timeStart){
            timeStart = time;
        }   

        let timeElapsed = time - timeStart; //    ()
        let complection = Math.min(timeElapsed/dur,1);//     

        cb(complection);// 

        if(timeElapsed < dur){
            requestAnimationFrame(_animateOverTime);
        }else{
            if(typeof fin === 'function'){
                fin();
            }   
        }
    }
    return _animateOverTime;
};

/**
 *  fadeIn -    
 * @param {*} dur  -  , 
 * @param {*} display 
 * @param {*} fin 
 * @return Object
 * @example
 * $('.butt').fadeIn(2000);
 * //=> {0: button.butt, length : 1};
 */
$.prototype.fadeIn = function(dur,display = 'block',fin){
    for(let i = 0; i < this.length; i++){
        // ,     
        this[i].style.display = display;

        let _fadeIn = (complection) =>{
            // ,    complection
            //  opacity  0  1
            this[i].style.opacity = complection;
        };

        let ani = this.animateOverTime(dur,_fadeIn,fin);
        requestAnimationFrame(ani);
    
    }
    return this;
};  

/**
 *  fadeOut -     
 * @param {*} dur 
 * @param {*} fin 
 * @return Object
 * @example
 * $('.butt').fadeOut(2000);
 * //=> {0: button.butt, length : 1};
 */
$.prototype.fadeOut = function (dur,fin){
    for(let i = 0; i < this.length; i++){

        let _fadeOut = (complection) =>{
            this[i].style.opacity = 1 - complection;// opacity
            if(complection === 1){
                // opacity - 0,    
                this[i].style.display = 'none';
            }
        };

        let ani = this.animateOverTime(dur,_fadeOut,fin);
        requestAnimationFrame(ani);
    }
    return this;
};

/**
 *  fadeToggle - ,      ,   .
 * @param {*} dur -  
 * @param {*} display 
 * @param {*} fin 
 * @returns Object
 * @example
 * $('.active').fadeToggle(1800);
 * //=> {0: button.active, 1: button.active, length: 2}
 * 
 * $('.btn-dark').on('click',function(){
 *  $(this).fadeToggle(1000);
 * });
 * //=> {0: button.btn-dark, length: 1}
 */
$.prototype.fadeToggle = function(dur,display,fin){
    for(let i = 0; i < this.length; i++){
        //        ,..    
        //  ,    css.     getComputedStyle
        if(window.getComputedStyle(this[i]).display ==='none'){
            $(this[i]).fadeIn(dur,display,fin);
        }else{
            $(this[i]).fadeOut(dur,fin);
        }
        }
    return this;
};