import $ from '../core';
/**
 *  Modal      .
 *     data-toggle="modal"      ,
 * data-ratget="#" -   ,      .
 * @param {boolean} -   ,     ,  .  .
 * @example
 * <a href="#" class=btn btn-primary" data-toggle="modal" data-target="#exampleModal">Link to</a>
 */
$.prototype.modal = function (created){
    function _calcScroll(){
        let div = document.createElement('div');

        div.style.width = '50px';
        div.style.height = '50px';
        div.style.overflowY = 'scroll';
        div.style.visibility = 'hidden';

        document.body.appendChild(div);
        let scrollWidth = div.offsetWidth - div.clientWidth;
        div.remove();
        
        return scrollWidth;

    }
    let scroll =_calcScroll();

    for(let i = 0; i < this.length; i++){
        let target = $(this[i]).getAttr('data-target');
        //target = #examplemodal

        $(this[i]).click((e)=>{
            e.preventDefault();
            $(target).fadeIn(500);
            
            document.body.style.overflow = 'hidden';
            if(document.body.offsetHeight > document.documentElement.clientHeight){
                //     ,   ,    
                document.body.style.marginRight =  `${scroll}px`;
            }
        });
        let closeElements = document.querySelectorAll(`${target} [data-close]`);
        closeElements.forEach(elem =>{
            $(elem).click(() => {
                $(target).fadeOut(500,()=>{
                    //  ,      
                    document.body.style.overflow = '';
                    document.body.style.marginRight =  `0px`;
                    if(created){
                        document.querySelector(target).remove();
                    }
                });
                
                
            });
        });
        $(target).click(e =>{
            if(e.target.classList.contains('modal')){
                $(target).fadeOut(500,()=>{
                    //  ,      
                    document.body.style.overflow = '';
                    document.body.style.marginRight =  `0px`;
                    if(created){
                        document.querySelector(target).remove();
                    }
                });
            }
        });
    }
};
$('[data-toggle="modal"]').modal();

/**
 * createModal  ,     ,
 *        .
 *   createModal   ,       .
 * @param {Object} {} -    text{
 * text -  title   ,  body -       
 *  title: "some text",
 *  body:   "some text"
 * }  button {
 *   ,        :
 * count: number,
 * settings:[
 *      [
 *          'text'-  ,
 *          ['',''] -    
 *          true/false -  boolean -    ,        
 *          ()=>{} -    
 *      ] 
 * ]
 * }
 *  
 * @example
 * $('#trigger').click(()=>$('#trigger').createModal({
    text: {
        title: 'Modal title',
        body: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Repellendus, officiis. Provident reiciendis odio error et corrupti, pariatur adipisci eligendi magnam ullam sapiente fugit culpa laborum nulla rerum possimus sed asperiores?'
    },
    btns:{
        count: 3,
        settings:[
            [
                'close',    
                ['btn-danger','mr-10'],
                true
            ],
            [
                'Save changes',
                ['btn-success'],
                false,
                ()=>{
                    alert(' ');
                }
            ],
            [
                'another button',
                ['btn-warning','ml-10'],
                false,
                ()=>{
                    alert('Hello World');
                }
            ]
        ]
    }
}));
 * 
 */

$.prototype.createModal = function({text , btns} = {}){
    //     js
    for(let i = 0; i < this.length; i++){
        let modal = document.createElement('div');
        //modal -    
        modal.classList.add('modal');
        $(modal).setAttr('id', $(this[i]).getAttr('data-target').slice(1)); // id,    
        //  ,   # ..    #example

        //btns = {count : num, settings: [[text, classNames=[],close,cb]]}
        //      
        let buttons = [];
        //      count 
        for(let j = 0; j < btns.count; j++ ){
            let btn = document.createElement('button');
            let [text,className,close,cb] = btns.settings[j];
            // , j -      , classNames - 
            //     spread    ,    classList
            btn.classList.add('btn', ...className);
            //    ,    
            btn.textContent = text;
            //        
            if(close){
                $(btn).setAttr('data-close','true');
            }
            if(btns.cb && typeof(cb) === 'function'){
                btn.addEventListener('click', cb);
            }
            buttons.push(btn);
        }

        //   ,        
        modal.innerHTML = `
            <div class="modal-dialog">
                <div class="modal-content">
                    <button class="close" data-close>
                        <span>&times;</span>
                    </button>
                    <div class="modal-header">
                        <div class="modal-title">
                            ${text.title}
                        </div>
                    </div>
                    <div class="modal-body">
                        ${text.body}
                    </div>
                    <div class="modal-footer">
                        
                    </div>
                </div>
            </div>
        `;

        //..       ,        ,
        //      html,      ,   ,   
        //   ,    ,      :
        //  append

        modal.querySelector(".modal-footer").append(...buttons);//   ,    
        //spread  
        document.body.appendChild(modal);// modal  
        //     
        $(this[i]).modal(true);//     
        $($(this[i]).getAttr('data-target')).fadeIn(500);//    
    }
};